// ========== >= 1200px ==========
@media screen and (max-width:1200px) {

	.wrapper {
		width: 100%; }

	// header
	header {
		.wrapper {
			.top {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				.nav {
					margin-top: 25px; } }
			.bottom {
				margin-left: 75px; } } }

	// main
	main {
		// help
		.help {
			.positions {
				width: 100%; } }

		// pets
		.pets {
			.pets-slider {
				width: 80%;
				.block {
					.block-img {
						img {
							// width: 100%
 } }							// height: auto
					.button {
						margin-bottom: 35px; } } } } }

	// footer
	footer {
		.wrapper {
			.info {
				margin-right: 75px;
				margin-left: 75px; }
			.block {
				width: 35%; } } }

	// modal
	.overflow {
		.katrine-cat-modal {
			.modal-window {
				.modal-right {} } } }

	// slider
	.slick-track {
		overflow: hidden;

 }		// slider buttons
	.left {
		left: -50px; }

	.right {
		right: -50px; } }

// ========== >= 992px ==========
@media screen and (max-width:992px) {

	// header
	header {
		padding: 50px 0 200px 0;
		.wrapper {
			.bottom {
				.title {
					font-size: 36px; } } } }

	// main
	main {
		// about-the-shelter
		.about-the-shelter {
			padding: 50px 0;
			.left-block {
				display: none; }
			.right-block {
				width: 90%;
				.title {
					text-align: center; }
				.text {
					text-align: justify; } } }

		// donation
		.donation {
			.left-block {
				display: none; }
			.right-block {
				width: 90%;
				text-align: center;
				.number-card {
					margin: 20px auto; } } }

		// pets-block
		.pets {
			.pets-slider {
				width: 80%;
				.block {
					.button {
						padding: 15px; } } } } }

	// footer
	footer {
		.wrapper {
			display: flex;
			align-items: center;
			.block {
				width: 50%;
				.title {
					font-size: 30px; }
				.e-mail, .number {
					span {
						font-size: 16px; } }
				.local-one, .local-two {
					span {
						font-size: 16px; } } } } }

	// modal
	.overflow {
		.katrine-cat-modal {
			.modal-window {
				flex-direction: column;
				justify-content: center;
				.modal-left {
					margin: 0 auto; } } } } }

// ========== >= 768px ==========
@media screen and (max-width:768px) {

	// main
	main {
		.pets {
			.pets-slider {
				.block {
					.button {
						padding: 15px 30px; } } } } }

	// modal
	.overflow {
		.modal-close {
			display: none; }
		.katrine-cat-modal {
			.modal-window {
				.modal-close-for-media {
					display: flex; } } } } }

// ========== >= 650px (for slider) ==========
@media screen and (max-width:650px) {

	main {
		// pets
		.pets {
			.pets-slider {
				.block {
					.block-img {
						img {
							width: auto;
							height: auto; } } } } } } }

// ========== <600px ==========
@media screen and (max-width:600px) {

	// main
	main {
		.help {
			.positions {
				.block-pos {
					width: 33%; } } } }

	// footer
	footer {
		.wrapper {
			width: 100%;
			display: flex;
			justify-content: space-around;
			.block {
				width: 45%;
				.title {
					text-align: center; } }
			.info {
				margin: 0; } } } }

// ========== <550px ==========
@media screen and (max-width:550px) {

	// header
	header {
		padding-bottom: 100px;
		background-position: 0% 0%;
		.wrapper {
			// burger-menu
			.burger-menu {
				display: block;
				// burger-menu__button
				.burger-menu__button {
					position: fixed;
					top: 20px;
					right: 20px;
					border-radius: 50%;
					border: 1px solid #000;
					background: rgba(0, 0, 0, 0.5);
					width: 50px;
					height: 50px;
					z-index: 30;
					&:hover {
						cursor: pointer; }
					&:hover .burger-menu__line {
						filter: brightness(0.7); }
					.burger-menu__line,
					.burger-menu__line::after,
					.burger-menu__line::before {
						position: absolute;
						background: #ffffff;
						width: 25px;
						height: 3px; }
					.burger-menu__line {
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%); }
					.burger-menu__line::after {
						content: '';
						top: 7px; }
					.burger-menu__line::before {
						content: '';
						top: -7px; } }
				// burger-menu__nav
				.burger-menu__nav {
					position: fixed;
					top: 0;
					right: -100%;
					width: 100%;
					background: #5a5a5a;
					color: #ffffff;
					overflow-y: auto;
					z-index: 20;
					ul {
						list-style-type: none;
						li {
							font-size: 20px;
							font-family: $Arial;
							text-transform: uppercase;
							padding: 25px 0;
							text-align: center;
							&:hover {
								transition: .5s;
								cursor: pointer;
								background: #4e4e4e; }
							&:first-of-type {
								margin-top: 80px; } } } } }
			// burger-menu_active
			.burger-menu_active {
				// burger-menu__button
				.burger-menu__button {
					&:hover .burger-menu__close {
						filter: brightness(0.7); }
					.burger-menu__line {
						display: none; }
					.burger-menu__close {}
					.burger-menu__close,
					.burger-menu__close::after,
					.burger-menu__close::before {
						position: absolute;
						width: 25px;
						height: 3px; }
					.burger-menu__close {
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%); }
					.burger-menu__close::after {
						content: '';
						top: 0px;
						transform: rotate(-45deg);
						background: #ffffff; }
					.burger-menu__close::before {
						content: '';
						top: 0px;
						transform: rotate(45deg);
						background: #ffffff; } }
				// burger-menu__nav
				.burger-menu__nav {
					transition: .3s;
					right: 0; }
				// burger-menu__overlay
				.burger-menu__overlay {
					transition: .3s;
					position: fixed;
					top: 0;
					left: 0;
					background: rgba(0, 0, 0, 0.7);
					width: 100vw;
					height: 100vh;
					z-index: 10; } }

			.top {
				.nav {
					display: none; } }
			.bottom {
				margin: 0;
				.title {
					width: 98%;
					text-align: center;
					margin-top: 40px; }
				.text {
					width: 98%;
					text-align: center; }
				.button {
					text-align: center; } } } }

	// main
	main {

		// pets
		.pets {
			.pets-slider {
				.block {
					margin: 10px 25px; } } }

		// help
		.help {
			.positions {
				.block-pos {
					width: 48%;
					text-align: center; } } }

		//donation
		.donation {
			.right-block {
				.title {
					margin-bottom: 40px; }
				.number-card {
					width: 100%;
					.number {
						font-size: 18px; } } } } }

	// footer
	footer {
		.wrapper {
			display: flex;
			flex-direction: column;
			.info {
				margin-bottom: 20px; }
			.block {
				width: 90%; } } }

	// slaider
	.left, .right {
		visibility: hidden; }

	// modal
	.overflow {
		.katrine-cat-modal {
			.modal-window {
				.modal-close-for-media {
					width: 40px;
					height: 40px;
					top: 1%; }
				.modal-left {
					width: 95%;
					height: auto;
					img {
						display: block;
						width: 100%;
						height: auto; } } } } } }
